<template>
  <v-app>
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="10" md="8" lg="6">
            <v-card>
              <v-form ref='form' @submit.prevent="signUp">
                <v-toolbar dark flat color="primary">
                  <v-toolbar-title>Sign Up</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-row>
                    <v-col cols="12">
                      <h2>
                        <v-avatar size="64" color="primary">
                          <v-icon color="white" size="32">fa-user</v-icon>
                        </v-avatar>
                      </h2>
                    </v-col>
                    <v-col cols="12" sm="6">
                      <v-text-field :rules="[rules.requires]" v-model="form.data.username" prepend-icon="fa-user"
                        label="Username" />
                    </v-col>
                    <v-col cols="12" sm="6">
                       <v-text-field :rules="[rules.requires]" v-model="form.data.email" prepend-icon="fa-envelope"
                          label="Email" />
                    </v-col>
                    <v-col cols="12">
                       <v-text-field disabled :placeholder="form.data.sponsor" prepend-icon="fa-user-circle"
                          label="Sponsor" />
                    </v-col>
                    <v-col cols="12" >
                      <v-text-field :rules="[rules.requires]" :type="form.control.password_display?'text':'password'"
                        prepend-icon="fa-lock" :append-icon="form.control.password_display?'fa-eye-slash':'fa-eye'"
                        @click:append="form.control.password_display=!form.control.password_display"
                        label="Password" placeholder="******" v-model="form.data.password" />
                    </v-col>
                  </v-row>
                </v-card-text>
                <v-card-actions>
                  <v-btn color="primary" :loading="form.control.loading" type="submit" >Create Account</v-btn>
                  <v-spacer />
                  <p>already have an account? </p>
                  <v-btn text color="secondary">Sign In</v-btn>
                </v-card-actions>
              </v-form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>



<script>
import img0 from "../assets/bg1.jpg";
import {rules} from '../plugins/rules'

export default {
  data() {
    return {
      bg0: img0,
      route: {
        state: 0,
        message: "",
        next: ""
      },
      type: 0,
      snackbar: {
        text: "",
        color: "success",
        value: false
      },
      form: {
        control: {
          password_display: false,
          loading: false,
          remember: false
        },
        data: {
          username: "",password: "",sponsor:'',email:''
        },
        error: {
          message: ""
        }
      },
      rules:rules
    };
  },
  computed: {},
  methods: {
    change_password_display() {
      this.form.control.password_display = !this.form.control.password_display
    },
    async signUp() {      
      if (this.form.control.loading) {
        return false;
      }
      this.form.error.message = "";
      console.log(this.form.data);
      
      if (this.$refs.form.validate()) {
        
        this.form.control.loading = true
        try {
          this.form.data.sponsor.length>=1?'':delete this.form.data.sponsor
          this.$store.dispatch('logOut')
          let response = await this.axios.post('/user/auth/register', this.form.data)
          this.$store.dispatch('login',response.data)
          this.$router.replace({path:'/account'})
        } catch (error) {     
          this.form.data.sponsor = ''     
          this.$refs.form.resetValidation()
          this.form.data.password = ''
          this.snackbar.value = true;
          this.snackbar.color = "error";
          this.snackbar.text =
            "SignUp failed due to connection error. please try again in a while";
          
        }
        this.form.control.loading = false
      } else {
        this.form.error.message = "Please input all fields";
      }

    },
    async validateSponsor(){
      
      if(!this.sponsor){return}
      try {
        let response = await this.axios.get(`user/exist/${this.sponsor}`)
        this.form.data.sponsor = this.sponsor    
      } catch (error) {}

    }
    
  },
  mounted() {
    this.validateSponsor()
  },
  props: {
    display: {
      default: 0
    },
    sponsor:{
      default:false
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to) {
        vm.route.state = 1;
        vm.route.message = to.params.message;
        vm.route.next = to.query.next;
      }
    });
  }
};
</script>
